import { APPLY_JOBS, GET_FAVORITE_LIST } from "@/api/api";
import successMessage from "@/components/popup/successMessage";
export default {
  name: "favoritedJobsDashboard",
  components: {
    successMessage
  },

  data() {
    return {
      page: 1,
      limit: 8,
      favoriteData: [],
      totalNum: 0,
      type: 1,
      selectJobId: 0,
      applyBtnLoading: false,
      applyVisible: false,
      applyTitle: '',
      applyDescription: ''
    };
  },

  mounted() {
    this.getFavoriteList(1, this.page, this.limit);
  },

  methods: {
    searchJobs() {
      this.$router.push('/jobs');
    },

    changeType(type) {
      this.type = type;
      this.favoriteData = [];
      this.page = 1;
      this.limit = 8;
      this.getFavoriteList(type, this.page, this.limit);
    },

    getFavoriteList(type, page, limit) {
      let params = {
        page: page,
        limit: limit
      };

      if (type) {
        params.type = type;
      }

      GET_FAVORITE_LIST(params).then(res => {
        console.log(res);

        if (res.code == 200) {
          this.favoriteData = res.message.data; // console.log(res.message.data)

          this.totalNum = res.message.total;
        }
      }).catch(err => {
        console.log(err);

        if (err.msg) {
          return this.$message.error(err.msg);
        }

        if (err.message) {
          return this.$message.error(err.message);
        }
      });
    },

    viewAll() {
      this.$router.push({
        path: '/favorites'
      });
    },

    applyJob(jobId, jobTitle) {
      let identity = localStorage.getItem('identity');
      let token = localStorage.getItem('token');
      this.selectJobId = jobId;

      if (token) {
        if (identity == 1) {
          let params = {
            job_id: jobId,
            token: token
          };
          APPLY_JOBS(params).then(res => {
            if (res.code == 200) {
              // this.$message.success('Apply Success')
              this.selectJobId = 0;
              this.applyVisible = true;
              this.applyTitle = 'Success';
              this.applyDescription = 'Your Application to ' + jobTitle + ' has been successfully sent.';
            }
          }).catch(err => {
            console.log(err); // this.$message.error(err.msg)

            this.applyVisible = true;
            this.applyTitle = 'Warning';
            this.applyDescription = err.msg;
            this.selectJobId = 0;
          });
        } else {
          this.applyVisible = true;
          this.applyTitle = 'Warning';
          this.applyDescription = 'Please switch to an educator profile to be able to apply'; // this.$message.warning('Please switch to an educator profile to be able to apply')

          this.selectJobId = 0;
        }
      } else {
        this.applyVisible = true;
        this.applyTitle = 'Warning';
        this.applyDescription = ' Please log in & fill in your profile to be able to apply.';
      }
    }

  }
};