import { computed } from 'vue';
import { useStore } from 'vuex';
import { useRouter } from 'vue-router';
import { MY_APPLY_JOBS } from "@/api/api";
import chatButton from "@/components/chat/chatButton";
export default {
  name: "applicationsUpdates",
  components: {
    chatButton
  },

  setup() {
    const store = useStore();
    const router = useRouter();

    const setNowChatUserInfo = data => store.commit('nowChatUserInfo', data);

    const setShowChatStatus = () => store.commit('showChatStatus', true);

    function turnChatPage() {
      router.push({
        path: '/chat/messages'
      });
    }

    const currentUser = computed(() => store.state.currentUser);

    function searchJobs() {
      router.push({
        path: '/jobs'
      });
    }

    return {
      searchJobs,
      setNowChatUserInfo,
      setShowChatStatus,
      currentUser,
      turnChatPage
    };
  },

  data() {
    return {
      myApplyJobData: [],
      page: 1,
      limit: 20
    };
  },

  mounted() {
    this.getMyApplyJobs(this.page, this.limit);
  },

  methods: {
    getMyApplyJobs(page, limit) {
      let params = {
        page: page,
        limit: limit
      };
      MY_APPLY_JOBS(params).then(res => {
        console.log(res);

        if (res.code == 200) {
          this.myApplyJobData = res.message.data;
        }
      }).catch(err => {
        console.log(err);
      });
    },

    chatSuccess() {
      this.turnChatPage();
    },

    turnMyApplications() {
      this.$router.push({
        path: '/jobs/home'
      });
    }

  }
};